main {
	padding-top: 150px;
}

::-webkit-scrollbar-track {
	box-shadow      : inset 0 0 6px rgba(255, 255, 255, 0);
	background-color: #fff;
}

::-webkit-scrollbar {
	width           : 5px;
	height          : 5px;
	background-color: #fff;
}

::-webkit-scrollbar-thumb {
	border-radius   : 2.5px;
	box-shadow      : inset 0 0 6px rgba(255, 255, 255, 0);
	background-color: $main-color-1;
}

.social-list {
	display: flex;

	nav {
		margin-left: 10px;
		display    : flex;
		align-items: center;

		a {
			flex           : 0 0 30px;
			max-width      : 30px;
			display        : flex;
			justify-content: center;
		}
	}
}

.btn {
	display   : block;
	background: #FFFFFF;

	color      : $main-color-2;
	font-weight: 700;
	text-align : center;

	box-shadow   : 0 3px 6px rgba($color: #000000, $alpha: 0.3);
	border-radius: 5px;
	transition   : .3s all ease-in-out;

	&.no-box-shadow {
		color     : #FFFFFF;
		background: $main-color-2;
		box-shadow: none;
	}

	&:hover {
		background: $main-color-2;
		color     : #FFFFFF;
	}

	@media (max-width: 767.98px) {
		width: 190px;
	}
}

.title {
	text-align    : center;
	letter-spacing: 3px;

	span {
		font-size: 17px;
		color    : $main-color-2;
	}

	h3 {
		font-size: 29px;
		color    : #434343;
	}
}

.home-product-item {
	margin-bottom: 80px;

	figure {
		cursor  : pointer;
		position: relative;
		height  : 423px;
		overflow: hidden;

		.box-img {
			position: relative;

			img {
				&:last-child {
					position  : absolute;
					bottom    : 30px;
					left      : 30px;
					transition: .5s all ease-in-out;
				}
			}
		}

		figcaption {
			position  : absolute;
			background: #FFFFFF;
			padding   : 40px;

			width : 295px;
			height: 210px;

			left     : -100%;
			top      : 50%;
			transform: translate(-50%, -50%);

			transition   : .5s all ease-in-out;
			border-radius: 5px;
			z-index      : 1;

			h5 {
				font-size : 21px;
				color     : #434343;
				text-align: center;
			}

			.btn {
				line-height: 42px;
				width      : 140px;
				margin     : 35px auto 0;
			}
		}

		&:after {
			content   : '';
			position  : absolute;
			width     : 100%;
			height    : 100%;
			top       : 0;
			right     : -100%;
			background: $main-color-1;
			opacity   : .5;
			transition: .5s all ease-in-out;
		}

		&:hover {
			.box-img {
				img {
					&:last-child {
						opacity: 0;
					}
				}
			}

			figcaption {
				left: 50%;
			}

			&:after {
				right: 0;
			}
		}
	}
}